.Friend {
  padding: 10px;
  width: 370px;
  display: flex;
  align-items: center;
  background: #ffffff;
  box-shadow: 0px 2px 1px rgba(0, 0, 0, 0.2), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 1px 3px rgba(0, 0, 0, 0.12);
  border-radius: 0px 0px 4px 4px;
}
.status {
  display: inline-block;
  width: 20px;
  height: 20px;
  margin: 4px;
  border-radius: 50%;
}
